import { Action, ActionPanel, Detail, Icon } from "@raycast/api";
import { useState } from "react";
import { renderToStaticMarkup } from "react-dom/server";
import Calendar from "./calendar";
import { microsoftDates } from "./dates";

export default function Command() {
  const [focusDate, setFocusDate] = useState(new Date());

  const focusYear = focusDate.getFullYear();

  // Figure out the current Microsoft FY, Q, H, and S from today's date
  const msftDates = microsoftDates(focusYear, focusDate.getMonth() + 1);

  // Render the pretty calendar as an SVG
  const rawSvg = renderToStaticMarkup(Calendar(msftDates.fy - 1, focusDate, msftDates));

  // To embed the SVG in markdown, we need to base64 encode it and wrap it in a data URI
  const encodedSvg = Buffer.from(rawSvg).toString("base64");
  const dataUri = `data:image/svg+xml;base64,${encodedSvg}`;

  const fyStr = `FY${msftDates.fy.toString().slice(2)}`;
  const focusDateStr = focusDate.toLocaleDateString("en-US", {
    month: "long",
    year: "numeric",
  });
  return (
    <Detail
      navigationTitle="Microsoft & GitHub Fiscal Year Calendar"
      markdown={`
<img src="${dataUri}">

According to the Microsoft & GitHub fiscal calendar, ${focusDateStr} is within:
- **Fiscal Year ${msftDates.fy}** (${fyStr})
- **Quarter ${msftDates.quarter}** (Q${msftDates.quarter}) of ${fyStr}
- **Half ${msftDates.half}** (H${msftDates.half}) of ${fyStr}
- **Semester ${msftDates.semester}** (S${msftDates.semester}), also known as ${msftDates.semesterName}
`}
      actions={
        <ActionPanel>
          <Action.PickDate
            title="Jump to date…"
            onChange={(date) => setFocusDate(date ?? new Date())}
            icon={Icon.Calendar}
          />
          <Action.CopyToClipboard title={`Copy "${fyStr}" to clipboard`} content={fyStr} />
          <Action.CopyToClipboard
            title={`Copy "Q${msftDates.quarter}" to clipboard`}
            content={`Q${msftDates.quarter}`}
          />
          <Action.CopyToClipboard title={`Copy "H${msftDates.half}" to clipboard`} content={`H${msftDates.half}`} />
          <Action.CopyToClipboard
            title={`Copy "S${msftDates.semester}" to clipboard`}
            content={`H${msftDates.semester}`}
          />
          <Action.CopyToClipboard
            title={`Copy "${msftDates.semesterName}" to clipboard`}
            content={`H${msftDates.semesterName}`}
          />
        </ActionPanel>
      }
    ></Detail>
  );
}
