export interface MicrosoftDates {
  fy: number;
  quarter: number;
  half: number;
  semester: number;
  semesterName: string;
}

// Convert a numeric year and month (1-indexed) to a MicrosoftDates object.
export function microsoftDates(year: number, month: number): MicrosoftDates {
  const fy = month < 7 ? year : year + 1;
  const half = month < 7 ? 2 : 1;

  // Get month in range [0, 11] then divide by 3 to get quarter in range [0, 3].
  // Offset by 2 to account for MSFT fiscal year, then wrap using % 4 and add 1
  // to get quarter in range [1, 4].
  const quarter = ((Math.floor((month - 1) / 3) + 2) % 4) + 1;

  // Semester 1 ran from April 1, 2022 to September 30, 2022. There are 2
  // semesters per year, so we take number of years since the semester epoch x
  // 2, and add an offset according to how far through the year we are.
  const semester = (year - 2022) * 2 + Math.floor((month - 1 - 3) / 6) + 1;
  const semesterName = semesterNames[semester - 1];

  return { fy, quarter, half, semester, semesterName };
}

const semesterNames = [
  "Copper",
  "Zinc",
  "Gallium",
  "Germanium",
  "Arsenic",
  "Selenium",
  "Bromine",
  "Krypton",
  "Rubidium",
  "Strontium",
  "Yttrium",
  "Zirconium",
  "Niobium",
  "Molybdenum",
  "Technetium",
  // If we reach the 2030s... PRs welcome!
];
