import { environment } from "@raycast/api";
import React from "react";
import { MicrosoftDates, microsoftDates } from "./dates";

// Beware: ugly SVG rendering code ahead 🐉

const width = 1000;
const gridXOffset = 70;
const gridYOffset = 25;
const gridWidth = width - gridXOffset;
const cols = 12;
const blockWidth = gridWidth / cols;
const blockHeight = 120;
const margin = 5;
const height = blockHeight * 2 + margin + gridYOffset;
const cellWidth = (gridWidth - (cols - 1) * margin) / cols;
const cellHeight = blockHeight - margin;
const months = [...Array(24).keys()];
const colors = {
  dark: {
    text: "#ffffff",
    periods: { fy: "#6BADE6", half: "#BA3D93", quarter: "#DEB253", semester: "#79CC9B" },
    monthBg: "#2e2a30",
    currentMonthStroke: "#d1cdd4",
    inactiveLabel: "#76707d",
  },
  light: {
    text: "#000000",
    periods: { fy: "#3F88EA", half: "#8D286C", quarter: "#BC682F", semester: "#3F8B3F" },
    monthBg: "#d0cdd4",
    currentMonthStroke: "#2e2a30",
    inactiveLabel: "#8f8b94",
  },
};

function themeColors() {
  return colors[environment.theme];
}

export default function Calendar(startYear: number, focusDate: Date, focusMsftDates: MicrosoftDates) {
  return (
    <svg width={width} height={height} xmlns="http://www.w3.org/2000/svg">
      <YearLabels startYear={startYear} />
      <MonthLabels />
      <g transform={`translate(${gridXOffset}, ${gridYOffset})`}>
        {months.map((m) => (
          <Month key={m} startYear={startYear} monthIndex={m} focusDate={focusDate} focusMsftDates={focusMsftDates} />
        ))}
      </g>
    </svg>
  );
}

interface PeriodLabelProps {
  period: "fy" | "half" | "quarter" | "semester";
  msftDates: MicrosoftDates;
  focusDates: MicrosoftDates;
}
function PeriodLabel({ period, msftDates, focusDates }: PeriodLabelProps) {
  const prefixes = { fy: "FY", half: "H", quarter: "Q", semester: "S" };
  const text = prefixes[period] + msftDates[period].toString().slice(period === "fy" ? 2 : 0);
  const matchers = { fy: sameFy, half: sameHalf, quarter: sameQuarter, semester: sameSemester };
  const fill = matchers[period](msftDates, focusDates) ? themeColors().periods[period] : themeColors().inactiveLabel;
  const yOffsets = { fy: 30, half: 55, quarter: 80, semester: 105 };
  return (
    <Text x={cellWidth / 2} y={yOffsets[period]} style={{ textAnchor: "middle" }} fill={fill}>
      {text}
    </Text>
  );
}

interface MonthProps {
  monthIndex: number;
  startYear: number;
  focusDate: Date;
  focusMsftDates: MicrosoftDates;
}
function Month({ monthIndex, startYear, focusDate, focusMsftDates }: MonthProps) {
  const year = startYear + Math.floor(monthIndex / 12);
  const month = (monthIndex % 12) + 1;
  const msftDates = microsoftDates(year, month);
  const currentMonth = focusDate.getMonth() + 1 === month && focusDate.getFullYear() === year;
  const x = blockWidth * (monthIndex % 12);
  const y = blockHeight * Math.floor(monthIndex / 12) + margin;
  return (
    <g transform={`translate(${x}, ${y})`}>
      <rect
        width={cellWidth}
        height={cellHeight}
        rx={5}
        ry={5}
        style={{
          fill: themeColors().monthBg,
          stroke: currentMonth ? themeColors().currentMonthStroke : undefined,
          strokeWidth: 3,
        }}
      />
      <PeriodLabel period="fy" msftDates={msftDates} focusDates={focusMsftDates} />
      <PeriodLabel period="half" msftDates={msftDates} focusDates={focusMsftDates} />
      <PeriodLabel period="quarter" msftDates={msftDates} focusDates={focusMsftDates} />
      <PeriodLabel period="semester" msftDates={msftDates} focusDates={focusMsftDates} />
    </g>
  );
}

function YearLabels({ startYear }: { startYear: number }) {
  return (
    <>
      <Text x={0} y={(blockHeight + 22) / 2 + gridYOffset}>
        {startYear}
      </Text>
      <Text x={0} y={(blockHeight + 22) / 2 + blockHeight + gridYOffset}>
        {startYear + 1}
      </Text>
    </>
  );
}

function MonthLabels() {
  const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
  return (
    <>
      {monthNames.map((month, i) => (
        <Text key={i} x={gridXOffset + blockWidth * i + blockWidth / 2 - 3} y={20} style={{ textAnchor: "middle" }}>
          {month}
        </Text>
      ))}
    </>
  );
}

type TextProps = React.PropsWithChildren<{ x: number; y: number; style?: React.CSSProperties; fill?: string }>;
function Text({ x, y, children, style, fill }: TextProps) {
  return (
    <text
      fill={fill ?? themeColors().text}
      x={x}
      y={y}
      style={{
        fontFamily: "-apple-system, BlinkMacSystemFont",
        fontSize: 20,
        fontWeight: "bold",
        ...style,
      }}
    >
      {children}
    </text>
  );
}

function sameHalf(a: MicrosoftDates, b: MicrosoftDates) {
  return a.fy === b.fy && a.half === b.half;
}

function sameQuarter(a: MicrosoftDates, b: MicrosoftDates) {
  return a.fy === b.fy && a.quarter === b.quarter;
}

function sameSemester(a: MicrosoftDates, b: MicrosoftDates) {
  return a.semester === b.semester;
}

function sameFy(a: MicrosoftDates, b: MicrosoftDates) {
  return a.fy === b.fy;
}
